#app {
    display: flex;
    justify-content: center;
    align-items: center;
}

#logo {
    border-bottom: 7px solid grey;
    border-right: 7px solid grey;
    border-radius: 5px 80px 100px 60px;
    font-size: 102px;
    font-family: Georgia, 'Times New Roman', Times, serif;
    font-weight: 1000;
    font-style: italic;
    padding: 5px 10px;
    
}

.product-word {
    border-bottom: 2px solid grey;
    border-right: 2px solid grey;
    border-radius: 5px 80px 100px 60px;
    font-family: Georgia, 'Times New Roman', Times, serif;
    font-weight: 1000;
    font-style: italic;
    padding: 5px 5px;
}